<template>
  <div class="box">
    <div class="desc">
        {{ gallup.desc }}</div>
    <div class="submit-button">
      <button @click="choose(gallup.id)">选择</button>
    </div>
    <span v-show="gallup.selected">
        {{ gallup.selectedCount }}
    </span>
  </div>
</template>
<script>
export default {
  data() {},
  props: {
    gallup: Object,
  },
  methods: {
    choose(id) {
      this.$emit("choose", id);
    },
  },
};
</script>

<style lang="css">
.box {
  display: inline-block;
  position: relative;
  margin: 5px;
  border: 1px solid yellowgreen;
  width: 250px;
  height: 100px;
  background-color: antiquewhite;
  padding: 5px;
}
span {
    display: block;
    position: absolute;
    width: 15px;
    border: 1px solid rgb(255, 187, 0);
    right: 3px;
    bottom: 3px;
    border-radius: 25%;
}
.box .desc {
    width: 250px;
}
.submit-button {
    width: 350px;
}
button {
  border: 1px;
  background-color: burlywood;
  width: 100px;
}
</style>


